 /* for desktop */

 a {
    text-decoration: none;
 }

 .whatsapp_float {
    position: fixed;
    width: 60px;
    height: 60px;
    bottom: 40px;
    right: 40px;
    background-color: #25d366;
    color: #FFF;
    border-radius: 50px;
    text-align: center;
    font-size: 38px;
    box-shadow: 2px 2px 3px #999;
    z-index: 100;
}

.whatsapp-icon {
    margin-top: 13px;
}

.whatsapp-icon:hover, .whatsapp_float:hover {
    color: #fff;
}

/* for mobile */
@media screen and (max-width: 767px) {
    .whatsapp-icon {
        margin-top: 10px;
    }

    .whatsapp_float {
        width: 50px;
        height: 50px;
        bottom: 20px;
        right: 15px;
        font-size: 22px;
        font-size: 32px;
    }
}
