  text-align: center;
  font-family: Poppins;
}
.join-text {
  color: white;
}
.join-title {
  padding-top: 10rem;
  text-align: center;
  font-family: Poppins;
  font-weight:600;
  letter-spacing: 0.5px;
}
.join-text div div {
  text-align: left;
  margin-left: 7rem;
