/* CSS for switch*/
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.level_switch {
    width: 54px;
    height: 28px;
}

.level_switch_padding {
    padding-left: 10px;
    margin-top: 8px;
}

/* Hiding the default checkbox*/
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #cccccc;
    transition: .3s;
}

/* Before sliding */
.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .3s;
}

.level_slider:before {
    width: 20px !important;
    height: 20px !important;
}

.toggle_input:checked+.slider {
    background-color: #234567;
}

.toggle_input:focus+.slider {
    box-shadow: 0 0 1px #564311;
}

.toggle_input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

